import React, { useState, useEffect } from 'react';
import './Dialogue.css';

//   URL   
const API_BASE_URL = 'https://living-hub-backend.onrender.com';

function Dialogue() {
  const [qa, setQa] = useState({ question: '', answer: '' });
  const [newMessageText, setNewMessageText] = useState('');

  //         
  const fetchQa = async () => {
    try {
      //   URL
      const response = await fetch(`${API_BASE_URL}/api/qa`);
      if (!response.ok) {
        //    ,  
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      const data = await response.json();
      setQa(data);
    } catch (error) {
      console.error("   :", error);
    }
  };

  useEffect(() => {
    //      
    fetchQa();
    //      3 
    const interval = setInterval(fetchQa, 3000);

    //  ,   
    return () => clearInterval(interval);
  }, []);

  //       
  const handleSendMessage = async (event) => {
    event.preventDefault();
    if (!newMessageText.trim()) return;

    try {
      //   URL  
      const response = await fetch(`${API_BASE_URL}/api/question`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ question: newMessageText }),
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      setNewMessageText('');
      //    fetchQa,   
      fetchQa();
    } catch (error) {
      console.error("   :", error);
      alert("     .");
    }
  };

  return (
    <div className="dialogue">
      <h2> </h2>
      
      <div className="qa-display">
        {qa.question && (
          <div className="question-section">
            <strong>:</strong>
            <p>{qa.question}</p>
          </div>
        )}
        {qa.answer && (
          <div className="answer-section">
            <strong> :</strong>
            <p>{qa.answer}</p>
          </div>
        )}
        {!qa.question && !qa.answer && (
            <p> .  ,  .</p>
        )}
      </div>

      <form onSubmit={handleSendMessage} className="send-message-form">
        <textarea
          value={newMessageText}
          onChange={(e) => setNewMessageText(e.target.value)}
          placeholder="   ..."
          rows="3"
          disabled={qa.question !== ''} //  ,    
        />
        <button type="submit" disabled={qa.question !== ''}>
           
        </button>
      </form>
    </div>
  );
}

export default Dialogue;
